import React, { forwardRef, useState } from "react";
import { Link, Redirect } from "react-router-dom";
import { Button, Typography } from "@mui/material";

import { useLocation } from "react-router-dom";
import InputOtp from "../Component/otp-master";

function Main(_, ref) {
    const { state } = useLocation();
    const [pwd, setPWD] = useState("");

    function handleOnChange(value) {
        setPWD(value);
    }

    return (
        <React.Fragment>
            {(!state?.location ?? true) && (!state?.address ?? true) && (
                <Redirect to="/" />
            )}
            <div className="title">
                <Typography variant="h5">인증번호 확인</Typography>
            </div>
            <Typography variant="caption" sx={{ maxWidth: 280 }}>
                계약자 핸드폰으로 인증번호가 발송 되었습니다.
                <br />
                문자로 받으신 인증번호를 입력해주시기 바랍니다.
            </Typography>
            <div className="title">
                <Typography variant="h5">인증번호 입력</Typography>
            </div>
            <div>
                <InputOtp
                    otpLength={4}
                    onChange={handleOnChange}
                    wrapperClass="otpWrapper"
                    inputWrapperClass="otpInputWrapper"
                    inputClass="otpInput"
                    errorMessageClass="errorMessage"
                />
            </div>
            <Button
                className="minWidth"
                variant="contained"
                component={Link}
                disabled={pwd.length !== 4}
                to={location => ({
                    pathname: "/beforepwd",
                    state: {
                        location: location.pathname,
                        address: state?.address,
                    },
                })}>
                확인
            </Button>
        </React.Fragment>
    );
}

export default forwardRef(Main);
