import React, { forwardRef } from "react";
import { Redirect } from "react-router-dom";
import { Button, Stack, Typography } from "@mui/material";
import { useLocation } from "react-router-dom";
import { isAndroid, isIOS } from "../plugin/device-detector";

const url = {
    ios: "https://apps.apple.com/kr/app/%EC%9E%90%EC%9D%B4/id1485517439",
    android:
        "https://play.google.com/store/apps/details?id=com.gs.xi",
};
function Main(_, ref) {
    const device = isAndroid ? "android" : isIOS ? "ios" : false;
    const { state } = useLocation();

    function handleOnClick() {
        window.location.replace(url[device]);
    }
    return (
        <React.Fragment>
            {(!state?.location ?? true) && (!state?.address ?? true) && (
                <Redirect to="/" />
            )}
            <div className="title">
                <Typography variant="h5">방문확인증(재방문)</Typography>
            </div>
            <Stack direction="column" spacing={1} alignItems="center">
                <Typography variant="h5">{state?.address}</Typography>
                <Typography variant="h5">세대 비밀번호: 1111</Typography>
                <Typography variant="subtitle1" sx={{ fontWeight: 100 }}>
                    동 출입구 비밀번호: 1234
                </Typography>
            </Stack>
            <Typography variant="h5" sx={{ pt: 2 }}>
                디지털도어록 여는 방법
            </Typography>
            <Stack direction="column" spacing={1}>
                <Typography variant="subtitle1" sx={{ fontWeight: 100 }}>
                    1. 키패드 손바닥 터치
                </Typography>
                <Typography variant="subtitle1" sx={{ fontWeight: 100 }}>
                    2. 키패드에 보이는 숫자 2개 누름
                </Typography>
            </Stack>
            <Typography variant="subtitle1" sx={{ fontWeight: 100 }}>
                행사 안내전화 02)000-000
            </Typography>
            <Stack alignItems="center">
                <Button
                    className="minWidth"
                    variant="contained"
                    onClick={handleOnClick}
                    disabled={!device}>
                    세대점검 시작
                </Button>
                {!device && (
                    <Typography variant="caption" color="GrayText">
                        지원하지않는 기기
                    </Typography>
                )}
            </Stack>
        </React.Fragment>
    );
}

export default forwardRef(Main);
