import UAParser from "ua-parser-js/dist/ua-parser.min";
const ClientUAInstance = new UAParser();
const os = ClientUAInstance.getOS();

const getNavigatorInstance = () => {
    if (typeof window !== "undefined") {
        if (window.navigator || navigator) {
            return window.navigator || navigator;
        }
    }

    return false;
};

const isIOS13Check = type => {
    const nav = getNavigatorInstance();
    return (
        nav &&
        nav.platform &&
        (nav.platform.indexOf(type) !== -1 ||
            (nav.platform === "MacIntel" &&
                nav.maxTouchPoints > 1 &&
                !window.MSStream))
    );
};

const isIOSType = ({ name }) => name === "iOS";
const isAndroidType = ({ name }) => name === "Android";
const getIPad13 = () => isIOS13Check("iPad");

export const isAndroid = isAndroidType(os);
export const isIOS = isIOSType(os) || getIPad13();
