import React, { forwardRef, useState } from "react";
import { Link, Redirect } from "react-router-dom";
import {
    Button,
    Checkbox,
    ListItemButton,
    ListItemIcon,
    ListItemText,
    Typography,
} from "@mui/material";

import { useLocation } from "react-router-dom";

function Main(_, ref) {
    const [checked, setChecked] = useState(false);
    const { state } = useLocation();

    function handleOnClick() {
        setChecked(v => !v);
    }

    return (
        <React.Fragment>
            {(!state?.location ?? true) && (!state?.address ?? true) && (
                <Redirect to="/" />
            )}
            <div className="title">
                <Typography variant="h5" sx={{ color: "#00192F" }}>
                    세대방문 시 유의사항
                </Typography>
            </div>
            <Typography variant="body1" sx={{ maxWidth: 280, fontWeight: 100 }}>
                <b>1. 세대 출입시</b> 제공되어지는 비밀번호는 계약자 본인만
                사용하며 타인에게 유출되지 않도록 유의바랍니다.
                <br />
                (세대 파손, 도난 방지)
            </Typography>
            <Typography variant="body1" sx={{ maxWidth: 280, fontWeight: 100 }}>
                <b>2. 재방문 시 반드시</b> 재접속 후 비밀번호를 재발급 받으시기
                바랍니다.
                <br />
                (행사 일자 별 비밀번호 변경됨)
            </Typography>
            <Typography variant="body1" sx={{ maxWidth: 280, fontWeight: 100 }}>
                <b>3. 동행가족분들은 반드시</b> 계약자분과 동행하여 입장하여
                주시기 바랍니다.
            </Typography>
            <ListItemButton onClick={handleOnClick}>
                <ListItemIcon>
                    <Checkbox checked={checked} />
                </ListItemIcon>
                <ListItemText primary="위 내용들을 모두 확인하였습니다." />
            </ListItemButton>
            <Button
                className="minWidth"
                variant="contained"
                component={Link}
                disabled={!checked}
                to={location => ({
                    pathname: "/pwd",
                    state: {
                        location: location.pathname,
                        address: state?.address,
                    },
                })}>
                확인
            </Button>
        </React.Fragment>
    );
}

export default forwardRef(Main);
