import React, { forwardRef, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { Button, CircularProgress, Paper, Typography } from "@mui/material";

import QRCode from "../plugin/QRGenerator";

import quickTitle from "../assets/quickTitle.svg";
import { userState } from "../store";

function Main(_, ref) {
    const [qr, setQR] = useState();
    const [bypass, setBypass] = userState(state => [
        state.bypass,
        state.setBypass,
    ]);
    useEffect(() => {
        setTimeout(() => {
            const isReEntered = localStorage.getItem("ctx_check") ?? false;
            if (!!isReEntered) setBypass(true);
            else
                setQR(
                    URL.createObjectURL(
                        new Blob(
                            [
                                new QRCode({
                                    content: "안녕하세요",
                                    padding: 4,
                                    width: 256,
                                    height: 256,
                                    color: "#000000",
                                    background: "#ffffff",
                                    ecl: "L",
                                    join: true,
                                }).svg(),
                            ],
                            { type: "image/svg+xml" }
                        )
                    )
                );
        }, 1000);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);
    return (
        <React.Fragment>
            <div className="title">
                <Typography variant="h5">자이</Typography>
                <img src={quickTitle} alt="quickTitle" className="logoImage" />
                <Typography variant="h5">입장서비스</Typography>
            </div>
            {bypass && (
                <div className="description">
                    <Typography variant="body2">해당 기기로</Typography>
                    <Typography variant="body2">
                        입장 등록을 이미 하셨습니다.
                    </Typography>
                </div>
            )}
            {!!qr && (
                <div className="description">
                    <Typography variant="body2">아래의 QR코드를</Typography>
                    <Typography variant="body2">
                        관리자에게 제시해 주시기 바랍니다.
                    </Typography>
                </div>
            )}
            {!qr && !bypass && (
                <Paper className="qrImg">
                    <CircularProgress size={60} />
                </Paper>
            )}
            {!!qr && <img src={qr} alt="QRcode" className="qrImg" />}
            <Button
                variant="contained"
                component={Link}
                to={location => ({
                    pathname: bypass ? "/select" : "/agree",
                    state: { location: location.pathname },
                })}
                disabled={!(!!qr || bypass)}>
                세대 비밀번호 발급
            </Button>
        </React.Fragment>
    );
}

export default forwardRef(Main);
