import React, { forwardRef, useState } from "react";
import { Link, Redirect, useLocation } from "react-router-dom";
import {
    Button,
    Checkbox,
    List,
    ListItemButton,
    ListItemIcon,
    ListItemText,
    Typography,
} from "@mui/material";

const items = [
    "301동 1101호",
    "101동 1102호",
    "401동 1105호",
    "201동 1108호",
    "101동 1109호",
];

function Main(_, ref) {
    const { state } = useLocation();
    const [data, setData] = useState();

    function handleOnClick(e) {
        setData(e.currentTarget.dataset.item);
    }

    return (
        <React.Fragment>
            {(!state?.location ?? true) && <Redirect to="/" />}
            <div className="title">
                <Typography variant="h5">계약자 동•호수 확인</Typography>
            </div>
            <List dense>
                {items.map(i => (
                    <ListItemButton
                        key={i}
                        data-item={i}
                        onClick={handleOnClick}>
                        <ListItemIcon>
                            <Checkbox checked={i === data} />
                        </ListItemIcon>
                        <ListItemText primary={i} />
                    </ListItemButton>
                ))}
            </List>
            <Typography variant="caption">
                고객님의 동•호수를 선택하신 후 '확인'버튼을 누르세요.
            </Typography>
            <Button
                className="minWidth"
                variant="contained"
                component={Link}
                disabled={!data}
                to={location => ({
                    pathname: "/check",
                    state: { location: location.pathname, address: data },
                })}>
                확인
            </Button>
        </React.Fragment>
    );
}

export default forwardRef(Main);
