import { BrowserRouter as Router, Route } from "react-router-dom";
import {
    CssBaseline,
    ThemeProvider,
    buttonBaseClasses,
    styled,
    typographyClasses,
    listItemTextClasses,
} from "@mui/material";

import backgroundImg from "./assets/gs/bottomPattern.svg";
import logo from "./assets/gs/logo.svg";
import { theme } from "./theme";
// import { routes, userState } from "./store";
// import "./App.css";

import RootPage from "./Page/CheckAdmin";
import AgreementPage from "./Page/Agreement";
import SelectApartPage from "./Page/SelectApart";
import CheckApartPage from "./Page/CheckApart";
import CheckUser from "./Page/CheckUser";
import AgreementBeforePwd from "./Page/AgreementBeforePwd";
import Password from "./Page/Password";

const BackgroundDiv = styled("div")(({ theme }) => ({
    position: "absolute",
    top: 0,
    right: 0,
    bottom: 0,
    left: 0,
    backgroundColor: "#EAE6E2",
    // backgroundImage: `url(${backgroundImg})`,
    // backgroundRepeat: "no-repeat",
    // backgroundSize: "cover",
    overflow: "hidden auto",
    display: "flex",
    [`& > div.bottomPattern`]: {
        position: "absolute",
        bottom: 0,
        left: 0,
        right: 0,
        height: 24,
        backgroundImage: `url(${backgroundImg})`,
        backgroundSize: "cover",
        backgroundRepeatX: "repeat",
        backgroundRepeatY: "no-repeat",
    },
    [`& > .wrapper`]: {
        width: "100%",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        margin: "auto",
        [`& > *`]: { margin: `${theme.spacing(2)} 0` },
        [`& > img.logo`]: {
            height: theme.typography.h4.fontSize,
            maxWidth: "100%",
            objectFit: "contain",
        },
        [`& > div.title`]: {
            display: "flex",
            alignItems: "flex-end",
            [`& > .${typographyClasses.root}`]: {
                fontWeight: 400,
            },
            [`& > img.logoImage`]: {
                height: theme.typography.h3.fontSize,
                objectFit: "contain",
                margin: `0 ${theme.spacing(1)}`,
                marginBottom: 4,
            },
        },
        [`& > div.description`]: {
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
        },
        [`& > img.qrImg`]: {
            borderRadius: "10%",
            objectFit: "contain",
            maxWidth: "80%",
            maxHeight: "90vh",
            padding: theme.spacing(2),
        },
        [`& > div.qrImg`]: {
            borderRadius: "10%",
            maxWidth: `min(80vw, 90vh)`,
            maxHeight: `min(80vw, 90vh)`,
            backgroundColor: "white",
            width: 256,
            height: 256,
            padding: theme.spacing(2),
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
        },
        [`& .${buttonBaseClasses.root}:not(.${listItemTextClasses})`]: {
            color: "#EAE6E2",
            [`&.${buttonBaseClasses.disabled}`]: {
                color: theme.palette.text.disabled,
            },
            [`&.rounded`]: {
                borderRadius: "20px/50%",
            },
            [`&.minWidth`]: {
                minWidth: 128,
            },
        },
    },
}));

function App() {
    return (
        <ThemeProvider theme={theme}>
            <CssBaseline />
            <BackgroundDiv>
                <div className="wrapper">
                    <img src={logo} alt="logo" className="logo" />
                    <Router basename={process.env.PUBLIC_URL}>
                        <Route exact path="/" component={RootPage} />
                        <Route exact path="/agree" component={AgreementPage} />
                        <Route
                            exact
                            path="/select"
                            component={SelectApartPage}
                        />
                        <Route exact path="/check" component={CheckApartPage} />
                        <Route exact path="/number" component={CheckUser} />
                        <Route
                            exact
                            path="/beforepwd"
                            component={AgreementBeforePwd}
                        />
                        <Route exact path="/pwd" component={Password} />
                        {/* <Route path="*" component={Fragment} /> */}
                    </Router>
                </div>
                <div className="bottomPattern" />
            </BackgroundDiv>
        </ThemeProvider>
    );
}

export default App;
